<template>
    <div class="common-address">
        <navigate title="常用地址" position="absolute"/>
        <div class="scroll-wrapper" ref="scrollWrapper">
            <mu-load-more :refreshing="refresh" :loading="loading"
                          @refresh="onRefresh"
                          @load="onLoad">
                <mu-sub-header style="color: #B4B4B4;">以下信息仅用于为您航线规划、推荐内容等</mu-sub-header>
                <mu-list style="margin-bottom: 30px; padding: 0; background-color: #fff;" :dense="false" v-if="list.length">
                    <swipe-cell :right-width="88" v-for="(item, index) in list" :key="index">
                        <mu-list-item class="border-1px-b" :ripple="false" @click.native="handlerItem(item)" button>
                            <mu-list-item-content>
                                <mu-list-item-title>{{item.name}}</mu-list-item-title>
                                <mu-list-item-sub-title v-if="item.address">{{item.address}}</mu-list-item-sub-title>
                            </mu-list-item-content>
                            <mu-list-item-action style="display: flex; justify-content: flex-end">
                                <mu-button class="list-item-button" color="#5B8CC0" @click="handlerLocation(item)" flat>
                                    定位
                                </mu-button>
                            </mu-list-item-action>
                        </mu-list-item>
                        <mu-button class="delete" slot="right" color="#fff" @click="handlerDelete(item, index)" flat>
                            删除
                        </mu-button>
                    </swipe-cell>
                </mu-list>
                <no-success text="您还没有添加常用地址" height="75vh" v-else/>
            </mu-load-more>
        </div>
        <div class="add-button-wrapper">
            <mu-button class="button" color="#fff" @click="saveAddress" flat>添加</mu-button>
        </div>
    </div>
</template>

<script>
    import Navigate from "../../../base/navigate/navigate"
    import {queryIAddress, deleteIAddress} from "../../../api/personal"
    import NoSuccess from "../../../components/no-success/no-success"
    import SwipeCell from 'vant/lib/swipe-cell'
    import 'vant/lib/swipe-cell/style'
    import {MapMixins} from "../../../common/js/mixins"

    export default {
        name: "common-address",
        mixins: [MapMixins],
        data() {
            return {
                /***
                 * 上拉刷新&下拉加载
                 */
                refresh: false,
                loading: false,
                /***
                 * 常用地址 列表
                 */
                list: [],
                /***
                 * 请求数据
                 */
                params: {
                    pageNumber: 0,
                    pageSize: 20
                }
            }
        },
        activated() {
            this.queryIAddress()
        },
        methods: {
            /***
             * 搜索常用地址
             */
            queryIAddress() {
                this.params.pageNumber = 0

                queryIAddress(this.params).then(res => {
                    this.list = res.content
                    this.refresh = false
                })
            },
            /***
             * 删除常用地址
             * @param item    => 删除对象
             * @param index   => 索引
             */
            handlerDelete(item, index) {
                deleteIAddress(item.id).then(res => {
                    this.$toast('删除成功')
                    this.list.splice(index, 1)
                })
            },
            /***
             * 下拉刷新
             */
            onRefresh() {
                this.refresh = true
                this.$refs.scrollWrapper.scrollTop = 0

                this.queryIAddress()
            },
            /***
             * 下拉加载
             */
            onLoad() {
                this.loading = true
                this.params.pageNumber++

                queryIAddress(this.params).then(res => {
                    this.list.push(...res.content)
                    this.loading = false
                })
            },
            /***
             * 定位
             * @param item   =>  定位对象
             */
            handlerLocation(item) {
                this.$toast({
                    message: '定位中 ...',
                    duration: 3000,
                    forbidClick: true
                })

                /***
                 * 设置地图中心点
                 */
                this.setCenter(item.longitude, item.latitude).then(res => {
                    this.$toast('定位成功')

                    // 定位成功后跳转至图层页
                    this.$router.push('/')
                }).catch(err => {
                    this.$toast('定位失败')
                })
            },
            /***
             * 修改&新增常用地址
             */
            saveAddress() {
                this.$router.push({
                    name: 'selection-cl'
                })
            },
            handlerItem(item) {
                // this.$router.push(`/detail/${item.typeId}/${item.id}`)
            },
            handlerSave() {
                this.onRefresh()
            }
        },
        components: {
            NoSuccess,
            Navigate,
            SwipeCell
        }
    }
</script>

<style lang="stylus" scoped>
    @import "../../../common/stylus/variable.styl"

    .common-address
        padding-top 46px
        background-color #EFEFEF
        .scroll-wrapper
            width 100%
            height 100%
            .list-item-title
                color #979797
                font-size $font-size-ml
            .list-item-address
                color #979797
                font-size $font-size-sl
            .list-item-button
                font-size $font-size-sl
            .delete
                width 100%
                height 100%
                background-color #f44336
        .add-button-wrapper
            position absolute
            bottom 15px
            left 15px
            right 15px
            .button
                width 100%
                background-color #3899d7
</style>



